class NavDots extends React.Component {
  constructor(props) {

    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.handleLeftArrow = this.handleLeftArrow.bind(this);
    this.handleRightArrow = this.handleRightArrow.bind(this);
  }

  handleRightArrow(event) {
    event.preventDefault();
    let dotActive = document.getElementsByClassName("dot_active");
    let nextDot = dotActive[0].nextSibling;

    if (!nextDot) {
      history.push("/")
    }
    else {
      history.push(nextDot.getAttribute('data-href'))
    }
    this.forceUpdate()
  }

  handleLeftArrow(event) {
    event.preventDefault();
    let dotActive = document.getElementsByClassName("dot_active");
    let prevDot = dotActive[0].previousSibling;

    if (!prevDot) {
      history.push("/contact")
    }
    else {
      history.push(prevDot.getAttribute('data-href'))
    }
    this.forceUpdate()
  }

  handleClick() {
    this.forceUpdate()
  }

  render() {
    let dotNormal = "dot"
    let dotActive = "dot_active"

    const routes = ['/', '/education', '/skills', '/contact']

    return (
      <Router history={history} >
        <a className="arrow-left" onClick={this.handleLeftArrow} href="/">❮</a>
        <a className="arrow-right" onClick={this.handleRightArrow} href="/">❯</a>


        <nav className="dotContainer">
          <Link to={routes[0]} className={"/" === window.location.pathname ? dotActive : dotNormal} name="dot" onClick={this.handleClick} data-href={routes[0]}></Link>
          <Link to={routes[1]} className={"/education" === window.location.pathname ? dotActive : dotNormal} name="dot" onClick={this.handleClick} data-href={routes[1]}></Link>
          <Link to={routes[2]} className={"/skills" === window.location.pathname ? dotActive : dotNormal} name="dot" onClick={this.handleClick} data-href={routes[2]}></Link>
          <Link to={routes[3]} className={"/contact" === window.location.pathname ? dotActive : dotNormal} name="dot" onClick={this.handleClick} data-href={routes[3]}></Link>
        </nav>

      </Router>
    )
  }
}

