class Timer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      date: Date.now(),
      timeDiffrence: "",
      hours: "",
      year: "",
      mins: "",

    }

  }

  componentDidMount() {
    this.timerID = setInterval(
      () => this.tick(),
      100
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
    let brithDate = new Date(1990, 11, 10)
    let hours = this.state.timeDiffrence * 2.7 * 0.0000001
    let year = this.state.timeDiffrence * 3.1688087814 * 0.00000000001
    let minsFromHours = hours % 1
    let mins = minsFromHours * 0.06 * 1000
    this.setState({
      date: Date.now(),
      timeDiffrence: Date.now() - brithDate,
      hours: Math.ceil(hours),
      year: Math.round(year),
      mins: Math.ceil(mins),
    })
  };

  render() {

    return (
      <section> Mam już {this.state.hours} godzin i {this.state.mins} minut życia za sobą z czego 25 godzin poświęciłem na stworzenie tej strony.</section>
    )
  }
}

